body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Used by App.js, ExampleRetriever.js, Menu.js */

:root {
  --overall: rgb(200, 220, 240);
  --header: rgb(80, 80, 240);
  --suggestions: rgb(200, 240, 240);
  --customVocab: rgb(171, 253, 171);
  --tableHeader: rgb(80, 100, 120);
  --tableRow: rgb(180, 200, 219);
  --tableVocabIncluded: rgb(240, 240, 240);
  --fontLight: ivory;
  --fontDark: black;
}


body {
  background-color: rgb(221, 209, 205);
  background-color: rgb(200, 220, 240);
  background-color: var(--overall);
}

/*ExampleRetriever*/
h1 {
  color: ivory;
  color: var(--fontLight);
  text-align: center;
  margin: 0;
}

h2 {
  text-align: center;

}

/*vocab*/

.div-header{
  background-color: rgb(80, 80, 240);
  background-color: var(--header);
  padding: 10px 10%;

  margin: 0;
}

.menu-buttons {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: row;
  min-height: 250px;
  padding: 30px, 10%;
}

.menu-buttons button {
  box-sizing: border-box;
  font-family: inherit;
  font-size: larger;
  font-weight: bold;
  text-align: center;
  width: 45%;
  margin: 10px;
}

.div-vocab {
  margin-top: 10px;
  display: flex;
}

.div-vocab-left, .div-vocab-right {
  width: 50%
}

.studentSelect {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: space-around;
  justify-items: center;
}

.studentSelect select, .studentSelect input{
  align-self: center;
  margin: 10px;
  font-size: large;
}

.quiz {
  height: 450px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
  padding: 2% 2%;
}

.exampleBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
}

.spanishExample, .englishTranslation {
  text-align: center;
  display: flex;
  flex-direction: column;
  vertical-align: center;
  justify-content: center;
  height: 250px;
  width: 40%;
  min-width: 300px;
  padding: 25px;
  border: 2px solid black;
  background-color: white;
  margin: 5px 10px;
}

.spanishExample p, .englishTranslation p {
  font-size: 18px;
  text-align: center;
}

.buttonBox {
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-items: center;
  margin: 10px;
}

.buttonBox button {
  margin: 0px 10px;
  display: block;
}

.progressBar2 {
  display:block;
  text-align: center;
  justify-self: center;
  padding: 10px;
}

/*.div-vocab-left {
  display: flex;
}*/


/* Top Left Search Bar */
.search {
  width: 46%;
  padding: 10px 0px;
}

/* Top Left 'Add to Search Query' Button */
.add-to-search-query {
  width: 50%;
  padding: 10px 0px;
}

/* Top Left List of suggested vocab */
.suggestions {
  background-color: rgb(200, 240, 240);
  background-color: var(--suggestions);
  color: black;
  height: 200px;
  overflow: auto;
  resize: vertical;
  list-style-type: none;
  font-weight: bold;
}
.suggestions li:hover {
  background-color: yellow;
}

/* Top Right Lesson select dropdown */
.lesson-select {
  display: inline-block;
  padding: 10px 0px;
}

/* Top Right 'Retrieve Sentences' button */
.retrieve-sentences {
  width: 50%;
  padding: 10px 0px;
  font-weight: bold;
}

/* Top Right - Vocab added on the right side, to be searched */
.custom-vocab {
  background-color: rgb(171, 253, 171);
  background-color: var(--customVocab);

  border: none;
  outline: none;
  padding: 6px;
  
  margin: 2px;
  border-radius: 10px;
}

.custom-vocab:hover {
  background-color: lightsalmon;
}

/* Bottom - Examples*/

.div-examples-header {
  display: flex;
  justify-content: space-between;
}

.div-examples-header button {
  padding: 8px;
}

.div-examples table {
  width: 100%;
  table-layout: fixed;
  vertical-align: baseline;
  font-weight: bold;
}

.div-examples th {
  background-color: rgb(80, 100, 120);
  background-color: var(--tableHeader);
  color:ivory
}

.div-examples tr:nth-child(even) {
  background-color: rgb(180, 200, 219);
  background-color: var(--tableRow);
}

/* underneath Vocab/Idioms */
.vocab-included-button {
  font-size: xx-small;
  padding: 1px 1px;
  margin: 0;
  color: black;

}
